<template>
    <div class="detail">
        <div class="box">
            <Header>
      <div slot="center">商品详情</div>
            </Header>
            <div class="cao">
                <div class="content">
                    <div v-if="flag">
                        <div class="img">
                            <img :src="list.Speli_logo_img" alt="">
                        </div>
                        <div class="info">
                            <h1>{{ proname }}</h1>
                            <p class="price">{{ list.money }}</p>
                            <p class="postage">
                              <span>快递： {{ list.yunfei }}</span>
                              <span>月销量：9856</span>
                              <span>上海区</span>
                            </p>
                            <div class="way">
                                <span>取货方式</span>
                                <p>  <span class="iconfont icon-jinakangbaoicons17">快递</span>
                                    <span class="iconfont icon-jinakangbaoicons17">通常</span>
                                    <span class="iconfont icon-jinakangbaoicons17">自提</span>
                                </p>
                            </div>
                        </div>
                        <div class="comment">
                            <h1>宝贝评价</h1>
                            <ul v-if="coflag">
                                <li v-for="item of commentlist" :key="item.commentid">
                                    <div class="top">
                                        <img :src="item.file" alt="暂无头像">
                                        <div class="right">
                                            <h4>{{ item.username }}:<van-rate v-model="item.flag" readonly count="3" class="rate" size="15px"/>
                                            </h4>
                                            <p class="time">{{item.time}}</p>
                                        </div>
                                    </div>
                                    <div class="bottom">
                                        <p>{{ item.text }}</p>
                                    </div>
                                </li>
                            </ul>
                            <p v-if="coflag === false">暂无评论</p>
                        </div>
                        <div class="particulars">
                            <span>详情</span>
                        </div>
                        <div class="pro-detail">
                            <p>产品详情</p>
                            <img :src="list.Speli_detail" alt="">
                        </div>
                       <div class="recommend">
                           <p>
                               店铺推荐
                           </p>
                           </div>
                       </div>
                    </div>
                </div>
            </div>
            <footer>
                <van-goods-action>
                    <van-goods-action-icon icon="chat-o" text="客服"/>
                    <van-goods-action-icon icon="cart-o" @click="toCart" text="购物车"  />
                    <!-- <van-goods-action-button type="warning" @click="addCart" text="加入购物车" /> -->
                    <van-goods-action-button type="danger" text="立即购买" @click="buy"/>
                    <van-goods-action-button type="warning" text="拼团" />
                </van-goods-action>
            </footer>
        </div>
</template>

<script>
import Header from '@/components/Header'
import Vue from 'vue'
import axios from '@/utils/request'
import {
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Toast,
  Dialog,
  Rate
} from 'vant'
Vue.use(Dialog)
Vue
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton)
  .use(Toast)
  .use(Rate)
export default {
  name: 'speli',
  components: {
    Header
  },
  data () {
    return {
      list: {},
      userid: '',
      flag: true,
      proid: '',
      proname: '',
      proimg: '',
      price: '',
      text: '',
      time: '',
      file: '',
      postage: '',
      commentlist: [],
      coflag: false,
      headimg: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=188149577,2949073731&fm=26&gp=0.jpg'
    }
  },
  mounted () {
    this.list = this.$route.params.item
    console.log(this.list)
  },
  methods: {
    back () {
      this.$router.back()
    },
    toCart () {
      this.$router.push('/cart')
    },
    buy () {
      let userid = localStorage.getItem('userid')
      let proid = this.proid
      let num = 1
      if (userid) {
        let url = '/cart/add?userid=' + userid + '&proid=' + proid + '&num=' + num
        axios.get(url).then(res => {
          // 如果未登录，跳转到登陆
          if (res.data.code === '10119') {
            this.$router.push('/login')
          } else {
            this.$router.push('/cart')
          }
        })
      } else {
        Dialog.alert({
          message: '您还没有登录，请前往登录'
        })
        this.$router.push('/login')
      }
    }
  },
  created () {
    const proid = this.$route.query.proid
    axios.get('/pro/detail?proid=' + proid).then(res => {
      if (res.data.code === '10119') {
        this.flag = false
      } else {
        this.flag = true
        this.proid = res.data.data.proid
        this.proname = res.data.data.proname
        this.proimg = res.data.data.proimg
        this.price = res.data.data.price
        this.note = res.data.data.note
        this.postage = res.data.data.postage
      }
    })
    axios.get('/comment?proid=' + proid).then(res => {
      if (res.data.data.length === 0) {
        this.coflag = false
      } else {
        this.coflag = true
        console.log(res.data.data[0].file)
        this.commentlist = res.data.data
        for (let item of res.data.data) {
          if (!item.file) {
            item.file = 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=188149577,2949073731&fm=26&gp=0.jpg'
          }
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
    @import '@/lib/reset.scss';
    .box{
        width: 100%;
        height: 100%;
        .cao{
            display: flex;
            justify-content: center;
            width: 100%;
            height: 100vh;
            overflow: auto;
            .content{
                height: 100%;
                width: 100%;
                background-color: white;
                .img{
                    width: 100%;
                    height: 375px;
                    display: flex;
                    justify-content: center;
                    img{
                        width: 98%;
                        height: 100%;
                    }
                }
                .info{
                    width: 98%;
                    margin-left: 6px;
                    height: 146px;
                    @include flexbox();
                    flex-direction: column;
                    justify-content: space-around;
                    margin-bottom: 20px;
                    h1{
                        letter-spacing: 1px;
                        font-weight:500;
                        font-size: 18px;
                        padding:20px 10px 0;
                    }
                    .way{
                        @include flexbox();
                        justify-content: space-between;
                        font-size: 10px;
                        color: #666;
                        margin-bottom:10px;
                        p{
                            width: 40%;
                            display: flex;
                            justify-content: space-around;
                            align-items: center;
                            span{
                                font-size: 10px;
                                color: orange;
                            }
                        }
                    }
                    .price{
                        color: #f66;
                        font-weight:bold;
                        font-size: 25px;
                    }
                    .postage{
                        color: #666;
                        font-size: 14px;
                        @include flexbox();
                        justify-content: space-between;
                        padding:5px 0;
                    }
                }
                .comment{
                    width: 98%;
                    margin-left: 6px;
                    h1{
                        font-size: 15px;
                        color: black;
                        font-weight: normal;
                    }
                    ul{
                        li{
                            margin-top: 15px;
                            .top{
                                @include flexbox();
                                align-items: center;
                                img{
                                    width: 33px;
                                    height: 33px;
                                    border-radius: 50%;
                                    display: inline-block;
                                }
                                .right{
                                    @include flexbox();
                                    flex-direction: column;
                                    justify-content: center;
                                    text-align: left;
                                    height: 30px;
                                    margin-left: 9px;
                                    h4{
                                        display: flex;
                                        align-items: center;
                                        font-size: 17px;
                                        color: palevioletred;
                                    }
                                    p{
                                        font-size: 10px;
                                        color: #ccc;
                                    }
                                }

                            }
                            .bottom{
                                p{
                                    font-size: 12px;
                                    margin-left: 42px;
                                    color: black;
                                    margin-top: 10px;
                                }
                            }
                        }
                    }
                }
                .particulars{
                    height: 38px;
                    width: 98%;
                    margin-left: 6px;
                    background-color: rgb(244, 244, 244);
                    text-align: center;
                    line-height: 38px;
                }
                .pro-detail{
                    width: 97%;
                    margin-left: 6px;
                    p{
                        height: 37px;
                        line-height: 37px;
                        border-bottom: 1px solid #ccc;
                    }
                    img{
                        width: 100%;
                    }
                }
                .recommend{
                    width: 97%;
                    margin-left: 6px;
                  p{
                      width: 100%;
                      height: 50px;
                      line-height: 50px;
                      font-size: 20px;
                      text-align: center;
                 }
                    .pic{
                        height: 180px;
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        .one{
                            width: 117px;
                            height: 180px;
                            display: flex;
                            flex-direction: column;
                           justify-content: space-around;
                            img{
                                width: 100%;
                                height: 130px;
                            }
                            p{
                                font-size: 12px;
                                color: #dc3e40;
                            }
                        }
                    }
               }
            }
        }
    }
  .van-goods-action {
    button {
      color:#fff;
    }
  }
</style>
